import React from "react";
import { NavLink, Outlet } from "react-router-dom";

const HostLayout = () => {
    const activeStyle = {
        fontWeight: "bold",
        textDecoration: "underline",
        color: "#161616",
    };
    return (
        <div>
            <nav className="host-nav">
                <NavLink
                    to="."
                    end
                    style={({ isActive }) => (isActive ? activeStyle : null)}
                >
                    Dashboard
                </NavLink>
                <NavLink
                    to="income"
                    style={({ isActive }) => (isActive ? activeStyle : null)}
                >
                    Income
                </NavLink>
                <NavLink
                    to="vans"
                    style={({ isActive }) => (isActive ? activeStyle : null)}
                >
                    Vans
                </NavLink>
                <NavLink
                    to="reviews"
                    style={({ isActive }) => (isActive ? activeStyle : null)}
                >
                    Reviews
                </NavLink>
            </nav>
            {/* The Outlet component renders the matching child route's `element` prop given in its route definition */}
            <Outlet />
        </div>
    );
};

export default HostLayout;
